html {
    font-size: 100%;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial,
    sans-serif;
    font-size: 1rem;
    line-height: 1.5;
}

button:disabled {
    opacity: 0.5;
}

#root {
    margin: 200px 0 500px;
}

.container {
    margin: auto;
    max-width: 1100px;
}

.new-figure-drag-helper {
    display: flex;
    position: relative;
}

#svg-render {
    border: 1px dotted grey;
}

.select-tool-wrap {
    position: relative;
}